      p.mb-5(data-aos='fade-right') Tener en cuenta las orientaciones generales para el manejo y abordaje de violencia fsica y sexual:
      .titulo-sexto.color-acento-contenido(data-aos='fade-right')
        h5 Figura 4. 
        span Orientacin para el manejo y abordaje de violencia fsica y sexual:
      .mn.bg-4.mb-4
        .row.justify-content-center.mb-5
          .col-lg-8
            img(src='@/assets/curso/temas/63.svg', alt='')
            
      .row.justify-content-center.mb-4
        .col-lg-8.mb-lg-0.mb-3(data-aos="fade-right")
          .tarjeta.p-5(style="background-color: #2c3e50 ").mb-4
            p.mb-0.text-white Durante la atencin en salud, es crucial detectar todas las situaciones de violencia que puedan condicionar o perpetuar las afecciones por las cuales consultan las personas LGBTIQ+ y con orientaciones e identidades de gnero no hegemnicas. Es importante recordar que la violencia sexual tambin puede ocurrir en contextos de actividades sexuales remuneradas, lo cual debe ser atendido conforme a las rutas vigentes y con los enfoques pertinentes.
          p.mb-3(data-aos='fade-right') Como complemento a los anteriores, se cuenta con el #[b(style="color: #0F5FFE ") Enfoque de desarrollo humano], por medio del cual se garantiza la creacin de los escenarios culturales, sociales y econmicos para el respeto y la realizacin de los derechos de los sectores sociales LGBTIQ+ y con orientaciones sexuales e identidades de gnero      no hegemnicas., permitiendo as, la potencializacin de las capacidades en torno a lo social, econmico y cultural; anclado esto al cambio de imaginarios que hacen parte de las vivencias. #[b(style="color: #0F5FFE ") (Ministerio del Interior, 2018). ] 
          .row.justify-content-center.align-items-center.mb-3
            .col-lg-1.d-lg-block.d-none(data-aos="fade-right"): img(src='@/assets/curso/temas/dots.svg', alt='')
            .col-lg-11
              p.mb-0 Una de las formas en la que se puede contribuir al Enfoque de desarrollo humano es en torno al desarrollo social, y es en este desarrollo en donde se pueden mejorar las condiciones de los servicios sociales bsicos, como la salud, nutricin, educacin primaria y planificacin familiar.

        .col-lg-4.col-7(data-aos="fade-left"): img(src='@/assets/curso/temas/64.png', alt='')

      p.mb-5(data-aos='fade-right') #[b(style="color: #0F5FFE ") Ejemplo] El personal de salud puede aportar al enfoque de desarrollo humano en la medida que genera recomendaciones en torno a los modos, condiciones y estilos de vida saludables. Puede basar las recomendaciones en su prctica y experiencia. Sin embargo, es importante tener en cuenta las recomendaciones dispuestas por el Ministerio de Salud y Proteccin Social respecto a:

      .row.justify-content-center.mb-5
        .col-lg-5.d-lg-block.d-none(data-aos="fade-right"): img(src='@/assets/curso/temas/65.png', alt='')
        .col-lg-7(data-aos="fade-left")
          .acordion-modificado 
            AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--acc rounded-0")
              div(titulo="Actividad fsica")
                p.mb-0 Declogo de la actividad fsica disponible en #[a(href="https://www.minsalud.gov.co/salud/publica/HS/Paginas/actividad-fisica.aspx"  target="_blank" ) https://www.minsalud.gov.co/salud/publica/HS/Paginas/actividad-fisica.aspx] Sugerir alternativas de actividad fsica que impliquen interacciones no sexistas, proponer roles equitativos en las actividades con todas las personas, hablar de referentes deportivos no hegemnicos e incluso sugerir la implementacin de ejercicios que no refuercen estereotipos de masculinidad y femineidad.
              div(titulo="Nutricin y alimentacin saludable")
                p.mb-0 Informacin sobre alimentacin y nutricin, lactancia materna, control de deficiencia de micronutrientes, alimentacin saludable, inocuidad y calidad de alimentos y poltica de seguridad alimentaria y nutricional en #[a(href="https://www.minsalud.gov.co/salud/publica/HS/Paginas/salud-nutricional.aspx"  target="_blank" ) https://www.minsalud.gov.co/salud/publica/HS/Paginas/salud-nutricional.aspx] 
                  br
                  |Fortalecer conceptos positivos sobre la imagen corporal para prevenir trastornos alimenticios. Evaluar calidad de dieta y riesgo de malnutricin.
              div(titulo="Valoracin del estado nutricional y estrategias")
                p.mb-0 Detectar problemas y diagnsticos nutricionales integrales. Definir estrategias de intervencin y seguimiento adecuado. Control por nutricionista acorde con necesidades puntuales identificadas.
              div(titulo="Indicadores nutricionales para personas trans")
                p.mb-0 La Academy of Nutrition and Dietetics recomienda el uso adecuado de indicadores nutricionales segn la fase de transicin. Los hombres Trans pueden tener demandas metablicas aumentadas debido al uso de testosterona.
              div(titulo="Prevencin del consumo de tabaco")
                p.mb-0 Prevencin del consumo de tabaco y evaluacin del uso de tabaco y otras sustancias en #[a(href="https://www.minsalud.gov.co/salud/publica/HS/Paginas/somos-mas-sin-tabaco.aspx"  target="_blank" ) https://www.minsalud.gov.co/salud/publica/HS/Paginas/somos-mas-sin-tabaco.aspx]
                  br
                  |Evaluar uso en los ltimos 6 meses, 12 meses y 5 aos.
              div(titulo="Peso saludable")
                p.mb-0 Consejos para mantener un peso saludable en #[a(href="https://www.minsalud.gov.co/salud/publica/HS/Paginas/peso-saludable.aspx"  target="_blank" ) https://www.minsalud.gov.co/salud/publica/HS/Paginas/peso-saludable.aspx]
                  br
                  |Evaluar dficit de vitaminas, caloras y micronutrientes en personas con dificultades en aumentar de peso o masa muscular, fatiga o ansiedad.
              div(titulo="Lavado de manos")
                p.mb-0 Importancia del lavado de manos detallada en #[a(href="https://www.minsalud.gov.co/salud/publica/HS/Paginas/lavado-de-manos.aspx"  target="_blank" ) https://www.minsalud.gov.co/salud/publica/HS/Paginas/lavado-de-manos.aspx]        
              div(titulo="Salud bucal, visual y auditiva")
                p.mb-0 Informacin sobre salud bucal, visual y auditiva en #[a(href="https://www.minsalud.gov.co/salud/publica/HS/Paginas/salud-bucal-visual-auditiva.aspx"  target="_blank" ) https://www.minsalud.gov.co/salud/publica/HS/Paginas/salud-bucal-visual-auditiva.aspx] 
                  br
                  |Hombres trans pueden tener sensibilidad, sangrado, inflamacin y dolor en las encas por el uso de testosterona. Evaluar enfermedades en tejidos blandos y duros, hbitos relacionados con la salud bucal.
      p.mb-5(data-aos='fade-right') #[b Enfoque de Orientaciones Sexuales e Identidades de Gnero Diversas.] Este enfoque que busca abordar y atender las diversas orientaciones sexuales e identidades de gnero, reconociendo y enfrentando la discriminacin, marginacin, exclusin y otras formas de violencia que afectan a las personas de los sectores LGBTIQ+. Se menciona en el Decreto 762/2018 en el contexto de la poltica pblica nacional LGBTI.
      .row.justify-content-center.mb-5
        .col-lg-4.mb-lg-0.mb-3
          .bg-white.tarjeta.overflow-hidden.sombra.h-100
            img(src='@/assets/curso/temas/66b.png', alt='')
            .p-4
              .text-center
                h5.mb-4 Poltica Pblica Nacional LGBTI
                p.mb-0 Poltica que parte de reconocer la vulneracin histrica de los derechos fundamentales de los sectores LGBTI, afirmando la igual dignidad y la condicin inalienable de sus derechos. Promueve el restablecimiento de derechos y la consecucin de la equidad bajo un enfoque diferencial.
        .col-lg-4.mb-lg-0.mb-3
          .bg-white.tarjeta.overflow-hidden.sombra.h-100
            img(src='@/assets/curso/temas/67b.png', alt='')
            .p-4
              .text-center
                h5.mb-4 Condiciones diferenciales 
                p.mb-0 Consideracin explcita dentro del enfoque que aborda cmo las condiciones especficas de las personas intersexuales afectan el ejercicio efectivo de sus derechos, reconociendo sus luchas y demandas relacionadas con vulneraciones en los escenarios de atencin de salud.
        .col-lg-4.mb-lg-0.mb-3
          .bg-white.tarjeta.overflow-hidden.sombra.h-100
            img(src='@/assets/curso/temas/68b.png', alt='')
            .p-4
              .text-center
                h5.mb-4 Objetivo de atencin en salud
                p.mb-0 Relacionado con la promocin y garanta del ejercicio de los derechos econmicos, sociales y culturales. Enfatiza en la igualdad de acceso y no discriminacin en servicios como salud, educacin, trabajo, vivienda, recreacin, deporte y cultura para personas LGBTI, adoptando medidas tcnicas y econmicas necesarias.
      
      figure.mb-5
        .video
          iframe(width="560" height="315" src="https://www.youtube.com/embed/pVl9PNZsJo8?si=VpSc7syq81ZC0_V0" title="El Enfoque de Orientaciones Sexuales e Identidades de Gnero Diversas" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
