              p.mb-4 Fomentar la comprensin de los principios de inclusin y respeto hacia las orientaciones sexuales e identidades de gnero diversas en el mbito de la salud.
              .tarjeta.bg-white.p-3
                .row.justify-content-around.align-items-center
