      p.mb-5(data-aos='fade-right') Desde este enfoque, el personal de salud debe desarrollar acciones y mecanismos afirmativos que garanticen una atencin con equidad independiente de la Orientacin Sexual e Identidad de Gnero. Para lo anterior, presentamos algunas acciones positivas para tener en cuenta: 
      TabsC.color-acento-botones.mb-5
        .p-5.py-md-5(titulo="Intimidad" style="background-color:#E8F0FF; margin-top: -15px")
          .row.justify-content-center.align-items-center
            .col-lg-6.mb-4.mb-md-0
              h4 Intimidad
              p Solicitar permiso para realizar cualquier exploracin, aunque sea un supuesto ordinario del proceso asistencial. En caso de que se trate de una persona adolescente acompaada por su padre, madre o persona cuidadora, tener en cuenta los procedimientos de atencin que ayuden a proteger su intimidad en el marco de la garanta de derechos sexuales y derechos reproductivos Tener presente que algunas preguntas o respuestas, pueden ser detonantes de violencia intrafamiliar en contextos homofbicos o transfbicos. Garantizar que durante la realizacin del examen fsico no ingresen otros profesionales de la salud sin el consentimiento de la persona.
              ul.lista-ul--color
                li.d-flex
                  i.fas.fa-heart
                  p.mb-0 Las personas trans pueden experimentar algn grado de incomodad respecto a sus cuerpos, y en algunos casos, el examen fsico puede      resultar traumtico. Es por esta razn que se recomienda, que, de no existir una necesidad mdica inmediata, se puede dilatar un poco la exploracin de mama, genitales y rectales, hasta haber establecido una fuerte relacin mdico-paciente y solo si dicha exploracin resulta mdicamente pertinente. En ningn caso realizar exploracin fsica o cuestionario sobre detalles de su orientacin sexual o identidad de gnero, por mera curiosidad, ni siquiera para expresar admiracin por el proceso de la persona.
                li.d-flex
                  i.fas.fa-heart
                  p.mb-0 Las personas trans y las personas intersex no son curiosidades mdicas y no deben ser objeto de revisiones innecesarias de parte de la persona tratante ni exhibidas ante otras personas profesionales de la salud. Esta vulneracin a la intimidad est claramente documentada por sus impactos negativos, especialmente en el caso de personas intersex y debe evitarse incluso en ambientes universitarios. Siempre se debe proceder desde el consentimiento de la persona, sin exhibirla y como monstruo, rareza o curiosidad.

                li.d-flex
                  i.fas.fa-heart
                  p.mb-0 Siempre se tiene que referir a la persona segn su identidad de gnero y con los pronombres de preferencia de la persona atendida. 
                //li.d-flex
                  i.fas.fa-heart
                  p.mb-0 No tratar a las personas trans e intersex como curiosidades mdicas ni exhibirlas ante otros profesionales. 
              //p.mb-5(data-aos='fade-right') Referir siempre a la persona por su identidad de gnero y pronombres preferidos.
            .col-lg-5.col-7: img(src='@/assets/curso/temas/69.png', alt='')
        .p-5.py-md-5(titulo="Confidencialidad" style="background-color:#E8F0FF; margin-top: -15px")
          .row.justify-content-center.align-items-center
            .col-lg-6.mb-4.mb-md-0
              h4 Confidencialidad
              p El usuario tiene derecho a la proteccin de datos de salud consignados en la historia clnica, implicando directamente a todos los profesionales que intervienen en el acto clnico.
              ul.lista-ul--color
                li.d-flex
                  i.fas.fa-heart
                  p.mb-0 No realizar comentarios sobre la identidad de gnero, orientacin sexual o expresin de gnero de la persona consultante con otras personas no relacionadas con el acto clnico; ni siquiera para expresar admiracin (esto se conoce como exotizacin). 
                li.d-flex
                  i.fas.fa-heart
                  p.mb-0 Lo anterior incluye la publicacin de informacin de casos puntuales a travs de redes sociales, incluso cuando se omita el nombre de la persona.
                li.d-flex
                  i.fas.fa-heart
                  p.mb-0 Asegurarse de que informacin sensible y que pueda derivar en tratos discriminatorios no aparezca en los resmenes de historia clnica u otros documentos usados para la realizacin de trmites administrativos, a menos que esto sea precondicin para acceder a un servicio. En las entidades debe implementarse capacitacin y sensibilizacin a personal administrativo para el manejo de dicha informacin para que se brinde un trato humanizado y sin discriminacin
              //p.mb-5(data-aos='fade-right') Asegurar que la informacin sensible no aparezca en resmenes de historia clnica u otros documentos administrativos a menos que sea necesario para acceder a un servicio. Implementar capacitacin y sensibilizacin al personal administrativo para manejar esta informacin de manera humanizada y sin discriminacin.
            .col-lg-4.col-7: img(src='@/assets/curso/temas/70.png', alt='')
        .p-5.py-md-5(titulo="Respecto al nombre identitario" style="background-color:#E8F0FF; margin-top: -15px")
          .row.justify-content-center.align-items-center
            .col-lg-6.mb-4.mb-md-0
              h4 Respecto al nombre identitario
              p En una situacin donde el nombre o la identidad de gnero de una persona no tenga coincidencia con la historia clnica, usted puede preguntar: Podra estar su historia clnica con un nombre distinto? 
              ul.lista-ul--color
                li.d-flex
                  i.fas.fa-heart
                  p.mb-0 Acorde con el ejemplo anterior, tener presente que no se debe preguntar a la persona cul es su nombre real. Formulada de esta manera, la pregunta es ofensiva, porque supone que el nombre identitario no corresponde con su realidad.
                li.d-flex
                  i.fas.fa-heart
                  p.mb-0 El personal de salud debe procurar el respeto de la identidad de gnero y la orientacin sexual, respetando los pronombres y nombre que la persona prefiera. Se debe emplear un lenguaje respetuoso e inclusivo.
                li.d-flex
                  i.fas.fa-heart
                  p.mb-0 Si el nombre identitario difiere del que se encuentra en el documento no debe ser una barrera para la atencin.     
                li.d-flex
                  i.fas.fa-heart
                  p.mb-0 Durante la atencin en ningn momento cuestionar la expresin de gnero (como la voz, los ademanes, la ropa, los accesorios o el cabello), entre otros.
                li.d-flex
                  i.fas.fa-heart
                  p.mb-0 De ninguna manera, el personal de salud puede generar juicios de valor frente a los roles de hombre y mujer ni reproducir estereotipos que puedan atentar contra la diversidad sexual y la dignidad humana.   
              //p No cuestionar la expresin de gnero en ningn momento, y evitar juicios de valor sobre roles de gnero o reproducir estereotipos que atenten contra la diversidad sexual y la dignidad humana.
            .col-lg-4.col-7: img(src='@/assets/curso/temas/71.png', alt='')
        .p-5.py-md-5(titulo="Respecto a la orientacin sexual" style="background-color:#E8F0FF; margin-top: -15px")
          .row.justify-content-center.align-items-center
            .col-lg-6.mb-4.mb-md-0
              h4 Respecto a la orientacin sexual
              p En toda consulta y consejera/orientacin no se debe presuponer la identidad de gnero de la pareja de la persona usuaria, o si se trata de una relacin monogmica (otro tipo de vnculos son posibles).

              ul.lista-ul--color
                li.d-flex
                  i.fas.fa-heart
                  p.mb-0 Durante la consulta con nios, nias y adolescentes se debe reconocer  las caractersticas nio, nia o adolescente vive en torno a su sexualidad. Se debe procurar en todo momento la comunicacin directa y no mediada por su pap, mam o por el acudiente responsable.

            .col-lg-4.col-7: img(src='@/assets/curso/temas/72.png', alt='')
      .tarjeta.p-5.mb-5(style="background-color: #2a3c52")
