                p.mb-0 Como parte de las polticas nacionales, los #[b(style="color: #0F5FFE ") Lineamientos de Transversalizacin de Enfoque de Gnero] buscan que, mediante el Enfoque Diferencial, se genere una respuesta institucional que parte del conocimiento de las situaciones o condiciones de vulnerabilidad social. Esta vulnerabilidad social nace a partir de las caractersticas de las personas y del contexto histrico, social y cultural, y puede determinar o incrementar la carga de desigualdad, generando diversas experiencias entre las personas para la garanta de sus derechos. En el marco del sector salud, este tipo de enfoque garantiza que las personas en condiciones de vulnerabilidad social, originada por sexo, gnero u orientacin sexual, puedan tener acceso a todos aquellos recursos y oportunidades indispensables para estar y permanecer de forma igualitaria en la garanta de sus derechos.
