      p.mb-5(data-aos='fade-right') Dentro del Enfoque Diferencial, se incorpora el #[b(style="color: #0F5FFE ") Enfoque Interseccional], responsable de articular y analizar la interaccin de varias categoras identitarias o caractersticas particulares, como #[b(style="color: #0F5FFE ") sexo, gnero, orientacin sexual, identidad de gnero, etnicidad, discapacidad, ruralidad, rol social o poltico, clase, etnicidad o raza, entre otras.] Este enfoque ayuda a comprender la interaccin de estas caractersticas con el fin de intervenirlas de manera conjunta y articulada (Ministerio del Interior, 2018).
