            p.mb-0.text-white "El gnero es la forma social que adopta cada sexo, toda vez que se le adjudican connotaciones especficas de valores, funciones y normas, o lo que tambin se llama, no muy acertadamente, roles sociales."
          p.mb-5(data-aos='fade-right') En el marco de los #[b(style="color: #0F5FFE ") Lineamientos de Transversalizacin del Enfoque de Gnero], este enfoque brinda la oportunidad de identificar las #[b(style="color: #0F5FFE ") desigualdades, discriminaciones y violencias] basadas en aspectos como #[b(style="color: #0F5FFE ") el sexo, la identidad de gnero y la orientacin sexual de las personas.] Asimismo, se observan las diferencias respecto al poder y los diversos roles que desempean mujeres y hombres en la vida familiar, la sociedad, el trabajo, la poltica y otros mbitos de interaccin humana, segn el Plan Decenal de Salud Pblica (PDSP) del MinSalud, 2022-2031.
            br
            br
            |Conforme a lo expuesto en el PDSP, el Enfoque de Igualdad de Gnero "facilita la visibilidad de todas las desigualdades, discriminaciones y violencias basadas en el sexo, la identidad de gnero y la orientacin sexual de las personas, as como las diferencias en el ejercicio del poder y los roles entre mujeres y hombres en la vida familiar, la sociedad, el trabajo, la poltica y otros mbitos de interaccin humana" (PDSP 2022-2031 / Res.2367 de 2023).
