            .tarjeta.h-100.p-5.d-flex.align-items-center(style="background-color: #DEE4F0")
              p.mb-0 Las personas de los sectores LGBTIQ+ y con orientaciones sexuales e identidades de gnero no hegemnicas, a menudo enfrentan situaciones de exclusin y violencia dentro de las comunidades y grupos tnicos, segn reporta la Comisin Interamericana de Derechos Humanos en el informe #[b(style="color: #0F5FFE ") "Violencia contra Personas Lesbianas, Gay, Bisexuales, Trans e Intersex en Amrica" (2015).] En ese informe, la Comisin menciona la discriminacin racial contra personas LGBTI en Colombia, Nicaragua, Estados Unidos, Brasil y los pases del Caribe Anglfono. Por un lado, estas situaciones surgen como producto de particularidades culturales o creencias dentro de la comunidad, lo que dificulta que la persona LGBTIQ+ exprese su identidad sin ser discriminada. Por otro lado, puede producirse una doble vulnerabilidad o discriminacin identitaria si esa comunidad es una minora que tambin sufre discriminacin. El Enfoque tnico reconoce este tipo de situaciones de exclusin y violencia dentro de las comunidades y grupos tnicos #[b(style="color: #0F5FFE ") (Ministerio del Interior, 2018).]
          .col-lg-5.mb-lg-0.mb-3: img(src='@/assets/curso/temas/95.png', alt='')
      .row.justify-content-center.mb-4
        .col-lg-4.col-7.mb-lg-0.mb-3(data-aos="fade-right"): img(src='@/assets/curso/temas/96.png', alt='') 
        .col-lg-8(data-aos="fade-left")
          p.mb-0 Dentro del Plan Decenal de Salud Pblica 2022-2031, se describe que el Enfoque tnico Diferencial est conformado por comunidades indgenas, negras, afrocolombianas, raizales, palenqueras y Rrom, quienes conservan su cosmovisin e identidad cultural. Para el Estado colombiano es crucial organizar el enfoque tnico diferencial en todos los planes, programas, proyectos y acciones que busquen responder a los requerimientos y necesidades manifestadas por estas comunidades, teniendo como base todas las diferencias tanto contextuales como territoriales e identitarias propias. Con este enfoque, se busca garantizar calidad, cobertura, vinculacin, participacin, accesibilidad e inclusin #[b(style="color: #0F5FFE ") (Resolucin 2367 de 2023).]
      p.mb-5(data-aos='fade-right') #[b(style="color: #0F5FFE ") El talento humano en salud puede tener en cuenta las siguientes recomendaciones abordar de manera adecuada el enfoque tnico diferencial durante la prestacin de los servicios en salud:]
      .row.justify-content-center.mb-5
        .col-lg-5.col-7.mb-lg-0.mb-3(data-aos="fade-right"): img(src='@/assets/curso/temas/97.png', alt='')
        .col-lg-7(data-aos="fade-left")
          .acordion-modificado 
            AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--acc rounded-0")
              div(titulo="Respeto a la cosmovisin")
                p.mb-0 Durante la prestacin del servicio de salud, sea respetuoso con la cosmovisin de las personas, particularmente si estas pertenecen a algn grupo tnico.
              div(titulo="Reconocimiento de expresiones culturales")
                p.mb-0 Generar servicios que incluyan opciones Transamigables, con personal capacitado y ambientes adaptados para el reconocimiento de sus necesidades e identidades.
              div(titulo="Uso de intrpretes y mediadores interculturales")
                p.mb-0 Previo a realizar la atencin, identifique si es necesario tener un intrprete o mediador intercultural, reconociendo la ruta metodolgica en caso de que se requiera.
              div(titulo="Material de apoyo en lenguas indgenas")
                p.mb-0 Algunas lenguas de grupos tnicos cuentan con diccionarios en espaol, por tanto, es recomendable que cuente con este material de apoyo en el lugar donde realiza la atencin.
              div(titulo="Aprendizaje de lenguas")
                p.mb-0 Previo a la atencin, organice actividades que le permitan conocer algunas de las palabras de la lengua para saludar, despedirse, hacer preguntas bsicas, agradecer y otras que considere relevantes dentro del proceso de atencin en salud.
              div(titulo="Adaptacin de conceptos tcnicos")
                p.mb-0 Cuando se pueda comunicarse con personas de los grupos tnicos en espaol, realice una adaptacin de los conceptos tcnicos a un lenguaje sencillo, claro y que tome en cuenta la vida cotidiana de la persona atendida, as como sus frases o palabras frecuentes.
      .row.justify-content-center.mb-5
        .col-lg-7.mb-lg-0.mb-3(data-aos="fade-left")
          .acordion-modificado 
            AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--acc rounded-0")
              div(titulo="Reflexin sobre estereotipos")
                p.mb-0 Tenga consciencia sobre los estereotipos y prejuicios que tiene sobre las comunidades indgenas, negras, afro, raizales, palenqueras y Rrom. Se recomienda la reflexin a nivel interno acerca de sus percepciones previas sobre la comunidad a la cual pertenece la persona atendida, reconociendo ampliamente todas las debilidades y fortalezas. Es importante formarse y estudiar constantemente para evitar estos estereotipos y prejuicios.
              div(titulo="Evitar juicios prejuiciosos")
                p.mb-0 Durante la prestacin del servicio, evite la emisin de juicios basados en prejuicios o estereotipos que vulneren la imagen, valores y cultura de los pueblos, comunidades y poblacin social y culturalmente diferenciada.
              div(titulo="Reconocimiento de prcticas tradicionales")
                p.mb-0 Para que la prestacin del servicio sea objetiva, reconozca las prcticas tradicionales que han existido o existen en la comunidad a la que pertenece la persona en torno a la salud y as complementar estas.
              div(titulo="Dilogo con grupos tnicos")
                p.mb-0 Est dispuesto a abrir espacios de dilogo con los individuos pertenecientes a grupos tnicos y sus comunidades para entender sus modos propios de vida y prcticas relacionadas con la salud.
              div(titulo="Respeto a la privacidad y confidencialidad en la diversidad")
                p.mb-0 Respete la confidencialidad de la informacin del individuo y favorezca espacios para su privacidad puesto que algunas de las barreras que tiene la poblacin LGBTIQ+ perteneciente a grupos tnicos tiene relacin con la invisibilizacin de su orientacin sexual e identidad de gnero por asuntos de tipo cultural.
        .col-lg-5.col-7(data-aos="fade-right"): img(src='@/assets/curso/temas/98.png', alt='')


      .bg-actividad-full.mn.mb-5
        .tarjeta.p-4.mb-0
          .row.justify-content-center.align-items-center.align-items-lg-stretch
            .col-6.col-md-4.col-lg-3.mb-md-0
