            .col-12.col-md-8.col-lg-9
              h2.txt-color-primario Actividad didctica 6
              h3 Enfoque tnico Diferencial
              p.mb-4 Reconocer la importancia de respetar y valorar las diferencias culturales y tnicas dentro de la sociedad.

              .tarjeta.bg-white.p-3
                .row.justify-content-around.align-items-center
