      p.mb-5(data-aos='fade-right') Otro de los aspectos importantes a tener en cuenta dentro del proceso de atencin en salud a la poblacin LGBTIQ+ y con orientaciones sexuales e identidades de gnero no hegemnicas, es el Enfoque de Curso de Vida. 

      .row.justify-content-center.align-items-center.mb-4
        .col-lg-6.mb-lg-0.mb-3(data-aos="fade-right")
          p #[b(style="color: #0F5FFE ") El enfoque de curso] de vida ha sido utilizado como elemento clave para la identificacin de las personas, tanto en el aspecto individual como en el colectivo. Este enfoque surgi como una propuesta interdisciplinaria en los mbitos histrico, social, econmico, demogrfico y cultural, con reconocimiento de la trayectoria vital en la organizacin de los estados de salud, que incluyen o no la ampliacin de libertades y logros en trminos de capacidades, bajo la idea fundante de la dignidad humana.
            
          .tarjeta.p-5.mb-5(style="background-color: #D5E3FF")
            .row.justify-content-center.align-items-center
              .col-lg-2.mb-lg-0.mb-3.col-7: img(src='@/assets/curso/temas/3.svg', alt='')
              .col-lg-10
                p.mb-0 Este enfoque tiene en cuenta la temporalidad y la organizacin de las relaciones que configuran a la persona a lo largo de su vida, considerando los escenarios de transformacin y la reflexin sobre la propia existencia, as como las relaciones creadas en el contexto social al que pertenece la persona.
        .col-lg-6.col-7: img(src='@/assets/curso/temas/201.png', alt='')

      .row.justify-content-center.align-items-center.mb-5
        .col-auto(data-aos="fade-right"): img(src='@/assets/curso/temas/dots.svg', alt='', style="width: 85px")
        .col
          p.mb-0 Hablar del enfoque de curso de vida permite tambin abordar las etapas de este en torno a las vulnerabilidades que se presentan de manera temporal, como son el desplazamiento, la pobreza, la enfermedad u otras condiciones que pueden afectar a la persona en cualquier edad.

      .row.justify-content-center.align-items-center.mb-4
        .col-lg-4: img(src='@/assets/curso/temas/202.png', alt='')
        .col-lg-8(data-aos="fade-right")
          .tarjeta.p-5(style="background-color: #423755")
            p.mb-0.text-white Segn lo expuesto por el Ministerio de Salud y Proteccin Social, el ciclo de vida permite entender las vulnerabilidades y las oportunidades de invertir durante etapas tempranas del desarrollo humano; reconoce que las experiencias se acumulan a lo largo de la vida, que las intervenciones en una generacin repercutirn en las siguientes y que el mayor beneficio de un grupo de edad puede derivarse de intervenciones previas en un grupo de edad anterior. Adems, facilita la mejora en el uso de recursos escasos, identificando riesgos, brechas y priorizando intervenciones clave.
        
      p.mb-5 El ciclo de vida se divide en las diversas etapas del desarrollo; dichas etapas estn marcadas por la diversidad individual y cultural. Las etapas identificadas en el ciclo de vida son:

      .row.justify-content-center.mb-5
