                  p.text-white Para entender y atender las directrices orientadas al cumplimiento del presente enfoque, es necesario comprender la diferencia entre las etapas del ciclo de vida y el curso de vida. Puede leer ms sobre este tema en el documentos " Diferencias entre ciclo vital y curso de vida.". 
                .col.mb-3.mb-sm-0
                  a.boton.color-acento-botones(:href="obtenerLink('downloads/Anexo_3.pdf')" target="_blank" type="application/pdf")
                    span Descargar 
