      p.mb-5(data-aos='fade-right') Dentro del proceso de atencin a las personas del sector LGBTIQ+ y con orientaciones sexuales e identidades de gnero diversas no hegemnicas, se deben tener en cuenta los siguientes elementos para aportar al enfoque de curso de vida:
      .row.justify-content-center.mb-5
        .col-lg-7.mb-lg-0.mb-3(data-aos="fade-left")
          .acordion-modificado 
            AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--acc rounded-0")
              div(titulo="Garantizar la atencin adecuada")
                p.mb-0 Asegurar que la atencin y el plan teraputico sean acordes con el ciclo y curso de vida al cual pertenece la persona.
              div(titulo="Ofrecer alternativas de tratamiento")
                p.mb-0 Permitir el control de los factores de riesgo identificados para la etapa de ciclo de vida y de las caractersticas identificadas en la trayectoria, momentos significativos o sucesos vitales, ventana de oportunidad y efectos acumulativos.
              div(titulo="Opciones educativas e intervenciones")
                p.mb-0 Dar todas las opciones al paciente de asistir a actividades educativas individuales o colectivas y de intervencin de factores de riesgo programadas por el grupo de apoyo de la entidad.
              div(titulo="Valoracin integral")
                p.mb-0 Valorar de manera integral a la persona teniendo en cuenta su ciclo y curso de vida, as como su identidad de gnero y orientacin sexual. Reconocer que la "familia social" puede ser crucial, especialmente debido a la discriminacin y exclusin que algunas personas LGBTIQ+ pueden experimentar.
              div(titulo="Identificar cambios en el desarrollo")
                p.mb-0 Identificar los principales cambios en el desarrollo (fsico, cognitivo, emocional, social).
              div(titulo="Deteccin temprana de alteraciones")
                p.mb-0 Detectar de forma temprana alteraciones que afecten negativamente la salud y el proceso de desarrollo.
              
        .col-lg-5.col-7(data-aos="fade-right"): img(src='@/assets/curso/temas/99b.png', alt='')
      .row.justify-content-center.mb-5
        .col-lg-5.col-7.mb-lg-0.mb-3(data-aos="fade-right"): img(src='@/assets/curso/temas/100b.png', alt='')
        .col-lg-7(data-aos="fade-left")
          .acordion-modificado 
            AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--acc rounded-0")
              div(titulo="Potenciar capacidades y habilidades")
                p.mb-0 Identificar y potenciar capacidades, habilidades y prcticas para promover el cuidado de la salud.
              div(titulo="Construccin del plan de cuidado")
                p.mb-0 Construir concertadamente con el paciente el plan de cuidado primario acorde con su ciclo y curso de vida, y las necesidades expuestas en la consulta.
              div(titulo="Consulta completa")
                p.mb-0 Durante la consulta, indagar de manera completa los antecedentes familiares y personales; respetar la libertad de la persona de no hablar respecto a los antecedentes familiares y/o personales.
              div(titulo="Indagar sobre riesgos y hbitos")
                p.mb-0 Indagar sobre la presencia de factores de riesgo, prcticas y hbitos saludables. Incluir preguntas sobre consumo y hbitos alimentarios, actividad laboral, ejercicio de derechos sexuales y reproductivos, riesgos psicosociales para violencias, y capacidades y recursos personales y familiares que inciden en el cuidado de la salud.
              div(titulo="Organizacin del plan de atencin")
                p.mb-0 Organizar todo el plan de atencin acorde con lo identificado en la anamnesis y teniendo en cuenta el ciclo y curso de vida.
              div(titulo="Activar ruta de atencin")
                p.mb-0 Activar la ruta de atencin que corresponda con el curso de vida de la persona
              div(titulo="Indicaciones teraputicas y de tratamiento")
                p.mb-0 Es esencial proporcionar las indicaciones teraputicas y de tratamiento considerando el plan de beneficios o tecnologas complementarias que la persona requiera, segn su diagnstico. Adems, es importante tener en cuenta la existencia de comorbilidades y condiciones asociadas a la edad para asegurar un manejo integral y adecuado.
      p.mb-5(data-aos='fade-right') Activar la ruta de atencin que corresponda con el curso de vida de la persona:      
      .titulo-sexto.color-acento-contenido(data-aos='fade-right')
