      p.mb-5(data-aos='fade-right') El Enfoque de Curso de Vida se entiende como el conjunto de trayectorias que tiene un individuo y su familia de acuerdo con los roles que desarrolla a lo largo de la vida (PDSP 2022-2031 / Res.2367 de 2023). Es as como la familia cobra importancia dentro del proceso de desarrollo y vivencias del individuo, dando vida al Enfoque de Salud Familiar y Comunitaria. Este enfoque se basa principalmente en reconocer a la familia como un grupo susceptible de cuidado, a quien se le debe garantizar la entrega de recomendaciones y herramientas dirigidas al autocuidado y la promocin de la salud. #[b(style="color: #0F5FFE ") Este enfoque tiene en cuenta principalmente los siguientes elementos:]
      .row.justify-content-center.mb-5
        .col-lg-4.mb-lg-0.mb-3
          .tarjeta.d-flex.align-items-center.p-5.h-100(style="background-color: #423755")
            p.mb-0.text-white.text-center Incentiva los estilos de vida saludables.
        .col-lg-4.mb-lg-0.mb-3
          .tarjeta.d-flex.align-items-center.p-5.h-100(style="background-color: #423755")
            p.mb-0.text-white.text-center Fomenta la responsabilidad familiar y comunitaria para mejorar las condiciones de salud.
        .col-lg-4.mb-lg-0.mb-3
          .tarjeta.d-flex.align-items-center.p-5.h-100(style="background-color: #423755")
            p.mb-0.text-white.text-center Durante procesos agudos de enfermedad, este tipo de enfoque busca garantizar la atencin en etapa temprana, evitando en todo caso el avance de la enfermedad a momentos de mayor dao y con tratamientos ms complejos (Ministerio de Salud y Proteccin Social).
      p.mb-5(data-aos='fade-right') Las recomendaciones que debe tener en cuenta el talento humano en salud para que el enfoque de salud familiar sea efectivo para las personas de los sectores LGBTIQ+ y con orientaciones sexuales e identidades de gnero no hegemnicas son las siguientes:     
      .row.justify-content-center.mb-5
        .col-lg-6.mb-lg-0.mb-3(data-aos="fade-right")
          .tarjeta.p-4(style="background-color: #3C1C74").mb-4
            p.mb-0.text-white Durante la atencin en salud, es crucial indagar a la persona sobre su red de apoyo familiar y comunitaria. Es importante reconocer que para algunas personas de los sectores sociales LGBTIQ+, la familia nuclear de origen no siempre representa un apoyo y aceptacin. En este contexto, la familia social, que tambin proporciona cuidados para la salud, cobra mucha importancia.
              br
              |Identificar la posicin o rol que ocupa la persona dentro de su familia, en caso de que cuente con ella.
          p.mb-5(data-aos='fade-right') #[b(style="color: #0F5FFE ") Entregar a la persona recomendaciones sobre hbitos saludables para s misma y para su familia, los cuales incluyen: ]
        .col-lg-6.col-7(data-aos="fade-right"): img(src='@/assets/curso/temas/101.png', alt='')
      .row.justify-content-center.mb-3
        .col-lg-3.mb-lg-0.mb-3
          .tarjeta.d-flex.align-items-center.p-5.h-100(style="background-color: #5F9EFC")
            p.mb-0.text-white.text-center Alimentacin equilibrada
        .col-lg-3.mb-lg-0.mb-3
          .tarjeta.d-flex.align-items-center.p-5.h-100(style="background-color: #5F9EFC")
            p.mb-0.text-white.text-center Prctica habitual de ejercicio
